@import "../../styles/colors";
@import "../../styles/fonts";

.card {
	height: 24rem;
	border-radius: 40px;
	border-style: groove;
	overflow: hidden;
	cursor: pointer;
	position: relative;
	color: $secondary;
	box-shadow: 0 10px 30px 5px rgba(0, 0, 0, 0.2);
	color: white;

	p {
		margin: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		font-size: 36px;
		font-family: "Roboto Slab", serif;
		font-optical-sizing: auto;
		font-weight: 300;
		font-style: normal;
	}

	img {
		object-fit: cover;
		width: 100%;
		height: 100%;
		opacity: 0.9;
		transition: opacity .2s ease-out;
	}
	
	&:hover img {
		transition: opacity .1s ease-in;
		opacity: 1;
	}

	@media (min-width: 400px) and (max-width: 1000px) {
		height: 20rem;
		width: 100%;
		display: flex;
		flex-direction: column;

		&:hover h2 {
			inset: auto auto 15rem 30px;
			transition: inset .3s ease-out;
		}
	}

	@media (max-width: 400px) {
		height: 20rem;
		width:100%;
		display: flex;
		flex-direction: column;

		h2 {
			transition: none;
		}

		&:hover h2 {
			opacity: 0;
		}
	}
	
}

.material-symbols-outlined {
vertical-align: middle;
}
